<template>
  <div class="farm">
    <van-tabs v-model="activeName"
              class="titleClass"
              @change="changeTab">
      <van-tab :title="$t('pool')"
               name="a">
        <Pool :Address="Address"
              @jump="jump"></Pool>
      </van-tab>
      <van-tab :title="$t('history')"
               name="b">
        <Historys :Address="Address"
                  ref="his"
                  @goToStakes="goToStakes"></Historys>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import Pool from './components/pool/index.vue'
import Historys from './components/History/index.vue'
export default {
  name: 'farm',
  components: {
    Pool,
    Historys
  },
  data () {
    return {
      activeName: 'a',
      formatAddress: ''
    }
  },
  computed: {
    Address () {
      return this.$store.state.address
    }
  },
  destroyed () {
    window.removeEventListener('popstate', this.backButton, false);//false阻止默认事件
  },
  mounted () {
    if (window.history && window.history.pushState) {
      history.pushState(null, null, document.URL);
      window.addEventListener('popstate', this.backButton, false);//false阻止默认事件
    }
  },
  methods: {
    backButton () {
      let tp = require('tp-js-sdk')
      if (tp.isConnected()) {
        tp.close()
      }
      console.log(12313, '返回')
    },
    goToStakes () {
      this.activeName = 'a'
    },
    jump () {
      this.activeName = 'b'
      this.changeTab()
    },
    changeTab () {
      if (this.$refs.his) this.$refs.his.getAddressInfo()
    }
  }
}
</script>

<style lang="less" scoped>
img {
  width: 100%;
  height: 100%;
  display: block;
}

.titleClass {
  /deep/.van-tab .van-tab__text {
    font-size: 17px;
    font-weight: 500;
    color: #909cba;
  }

  /deep/.van-tab.van-tab--active .van-tab__text {
    font-weight: bold;
    color: #1f75d9;
  }

  /deep/.van-tabs__line {
    height: 2px;
    width: 15px;
    background-color: #1f75d9;
  }

  /deep/.van-tabs__content {
    background-color: #f6f7fb;
    padding: 5px 10px;
  }
}
</style>
