<template>
  <div class="History">
    <van-list v-model="loading"
              :finished="finished"
              finished-text="">
      <div class="box">
        <div class="item"
             v-for="(item, index) in list"
             :key="index">
          <div class="num">
            <div class="money">
              <div class="lp">{{item.capital / Math.pow(10, 18) | NumFormat}}</div>
              <div class="unit">{{$t('lp')}}</div>
            </div>
          </div>
          <div class="theNumberOf">
            <div class="time">
              <div class="day">{{item.day}}{{$t('days')}}</div>
            </div>
            <div class="number">{{item.num}}{{$t('times')}}</div>
          </div>
          <div class="profit">
            <div class="number">{{item.historyAward / Math.pow(10, 18) | NumFormat}}</div>
            <div class="tips">{{$t('harvest')}}</div>
          </div>
          <div class="date">{{item.time}}</div>
          <van-button type="info"
                      @click="finisheds(item.day, item.redeem, index)"
                      :disabled="!item.redeem"
                      :loading="item.loading"
                      class="btn"
                      color="#1F75D9">{{$t('redeem')}}
          </van-button>
        </div>
      </div>
    </van-list>
    <emptys :showEmpty="showEmpty"
            :image="images"
            btnText="goToStake"
            @emptyBtnClick="goToStake"
            emptyTip="historyEmptyTip"></emptys>
  </div>
</template>

<script>
import {
  Toast
} from 'vant';
import {
  NumFormat,
  getUnixToDate
} from '@/util/filter.js'
import images from '@/assets/history.png'
import emptys from '@/components/empty/index.vue'
export default {
  name: 'History',
  props: ['Address'],
  components: {
    emptys
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      showEmpty: false,
      images: images,
      WEB: '',
      userAddress: ''
    }
  },
  filters: {
    NumFormat
  },
  watch: {
    'Address': function (val) {
      this.userAddress = val
      this.getAddressInfo()
    }
  },
  mounted () {
    this.WEB = this.$store.state.WEB3
    if (this.Address) {
      this.userAddress = this.Address
      this.getAddressInfo()
    }
  },
  methods: {
    // 去质押
    goToStake () {
      this.$emit('goToStakes')
    },
    // 赎回
    async finisheds (day, state, index) {
      if (!state) return
      try {
        this.list[index].loading = true
        this.list[index].redeem = false
        await this.$store.state.poolContractInstance.methods.unStake(day).send({
          from: this.userAddress
        }).then((res) => {
          Toast(this.$t('success'))
          this.list.splice(index, 1)
          this.$bus.emit("redeemBusEvent");
          console.log('赎回', res)
        })
      } catch (e) {
        //TODO handle the exception
        Toast(this.$t('cancel'))
        this.list[index].loading = false
        this.list[index].redeem = true
      }


    },
    onRefresh () {
      this.finished = false
      this.loading = true;
    },
    // 获取质押历史
    async getAddressInfo () {
      let time = ''
      this.loading = true;
      this.finished = false;
      let nowBlockNumber = await this.WEB.eth.getBlockNumber()
      let poolContractInstanceMethods = this.$store.state.poolContractInstance.methods
      console.log('当前块nowBlockNumber', nowBlockNumber)
      let infoList = []
      // capital 用户本金
      // historyAward 历史累计收割奖励
      // accAward 累计奖励
      // stakeBlock 用户质押开始块
      // redeem（当前块>用户质押开始块+用户质押周期）是否可以赎回
      let Info_30 = await poolContractInstanceMethods.getAddressInfo(this.userAddress, 30).call()
      let Info_60 = await poolContractInstanceMethods.getAddressInfo(this.userAddress, 90).call()
      let Info_90 = await poolContractInstanceMethods.getAddressInfo(this.userAddress, 180).call()
      let Info_365 = await poolContractInstanceMethods.getAddressInfo(this.userAddress, 365).call()
      if (Info_30.capital != 0) infoList.push(Info_30), Info_30.day = 30
      if (Info_60.capital != 0) infoList.push(Info_60), Info_60.day = 90
      if (Info_90.capital != 0) infoList.push(Info_90), Info_90.day = 180
      if (Info_365.capital != 0) infoList.push(Info_365), Info_365.day = 365
      for (let i in infoList) {
        infoList[i].loading = false
        let nowBlock = await this.WEB.eth.getBlock(infoList[i].stakeBlock)
        time = getUnixToDate('yyyy-mm-dd hh:mm:ss', nowBlock.timestamp)
        // console.log('当前块时间', nowBlock.timestamp)
        infoList[i].time = time
        infoList[i].timestamp = nowBlock.timestamp
        infoList[i].redeem = nowBlockNumber > (+infoList[i].stakeBlock + infoList[i].day * 28800) ? true : false
        infoList[i].num = (infoList[i].stakePower / infoList[i].capital).toFixed(2)
      }
      console.log('获取质押历史', infoList)
      // 排序
      function setArray (infoList) {
        infoList.sort(compare('timestamp'))
        return infoList
      }

      function compare (property) {
        return function (a, b) {
          return b[property] - a[property]
        }
      }
      this.list = setArray(infoList);
      this.loading = false;
      this.finished = true;
      if (this.list.length == 0) {
        this.showEmpty = true
      }
    }
  }
}
</script>

<style scoped lang="less">
img {
  width: 100%;
  height: 100%;
  display: block;
}

.box {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr;

  .item {
    display: flex;
    flex-direction: column;
    background: #ffffff;
    border-radius: 6px;
    padding: 15px;
    padding-top: 20px;

    .date {
      font-size: 12px;
      font-weight: 500;
      color: #98a3bf;
    }

    .btn {
      width: 90px;
      height: 27px;
      background: #1f75d9;
      // background: #DEDEDE;
      border-radius: 3px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      font-weight: 500;
      color: #ffffff;
      margin: auto;
      margin-top: 15px;
    }

    .disable {
      background: #dedede;
    }

    .profit {
      display: flex;
      margin-bottom: 15px;
      align-items: flex-end;

      .number {
        font-size: 19px;
        font-weight: bold;
        color: #1f75d9;
        margin-right: 8px;
        line-height: 1;
      }

      .tips {
        flex-shrink: 0;
        font-size: 12px;
        font-family: PingFang SC;
        font-weight: 500;
        transform: scale(0.75);
        transform-origin: left;
        color: #98a3bf;
      }
    }
    .theNumberOf {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 15px;
      .time {
        width: 42px;
        height: 17px;
        background: #909cba;
        border-radius: 9px;
        font-weight: 500;
        display: flex;
        text-align: center;
        justify-content: center;

        .day {
          font-size: 12px;
          color: #ffffff;
          transform: scale(0.75);
          line-height: 1;
          display: flex;
          align-items: center;
        }
      }
      .number {
        font-size: 14px;
        font-weight: 500;
        color: #2a2d37;
      }
    }
    .num {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 15px;

      .money {
        display: flex;
        align-items: flex-end;

        .lp {
          font-size: 14px;
          font-weight: bold;
          color: #2a2d37;
        }

        .unit {
          margin-left: 9px;
          font-size: 12px;
          font-weight: 500;
          color: #98a3bf;
          transform: scale(0.75);
          transform-origin: left;
        }
      }
    }
  }
}
</style>
