<template>
  <div class="pool">
    <div class="content">
      <div class="type">
        <div class="star">
          <img src="@/assets/head_Star.png" />
        </div>
        <div class="names">SRS</div>
        <div class="add">
          <img src="@/assets/add.png" />
        </div>
        <div class="bnb">
          <img src="@/assets/bnb.png" />
        </div>
        <div class="names">BNB</div>
      </div>
      <div class="moneyList">
        <div class="money srsBg"
             :style="`background-image: url(${require('@/assets/srs_bg.png')});`">
          <span class="number">
            <span>{{ allResidualReward | NumFormat('integer') }}/50,000,000</span>
            <div class="pos">SRS</div>
          </span>
        </div>
        <div class="money pool2"
             @click="userDestroy">
          <span class="number">
            <span>Pool 2.0 {{ $t('isAboutToOpen') }}</span>
            <span>50,000,000SRS</span>
          </span>
        </div>
        <div class="money pool2">
          <span class="number">
            <span>Pool 3.0 {{ $t('isAboutToOpen') }}</span>
            <span>50,000,000SRS</span>
          </span>
        </div>
      </div>
      <div class="lp">{{ $t('myLP') }} : {{ balance }}</div>
      <!-- <div class="sliderNum">
        <div class="slider">
          <div class="sumTo">
            <van-slider
              v-model="percentage"
              :disabled="inputDisabled"
              @input="percentageChange"
              active-color="#407BDD"
              inactive-color="#909CBA"
              step="10"
              bar-height="4px"
            >
              <template #button>
                <div class="custom-button">
                  <div class="schedule">{{ percentage }}%</div>
                  <div class="circle"></div>
                </div>
              </template>
            </van-slider>
            <div class="rigNum">{{ number | NumFormat }}</div>
          </div>
          <div class="all" @click="allstake">
            {{ $t('all') }}
          </div>
          <div class="mark" @click="showDayTips = true">
            <img src="../../assets/mark.png" />
          </div>
        </div>
        <div class="leftMultiple">
          {{ $t('hashrate') }} : {{ calculateForceMultiplier }}{{ $t('times') }}
        </div>
      </div> -->

      <div class="input">
        <div class="box">
          <van-field class="field"
                     v-model.number="number"
                     @input="inputChange"
                     type="number"
                     label=""
                     placeholder="0.00"
                     :disabled="inputDisabled"></van-field>
          <!-- <div class="all" @click="inputDisabled ? '' : (number = balance)">
            {{ $t('all') }}
          </div> -->
        </div>
        <div class="mark"
             @click="showDayTips = true">
          <img src="@/assets/mark.png" />
        </div>
      </div>
      <div class="changeSlider">
        <div class="lines"
             v-for="(el, i) in linesList"
             :key="i"
             @click="activeLineChange(i, el.value)"
             :class="activeLine !== '' && activeLine >= i ? 'active' : ''">
          <div class="slide"></div>
          <div class="nums">{{ el.lable }}</div>
        </div>
      </div>
      <div class="hashrates">
        <div class="myHashrates">{{ $t('hashrate') }} : {{ calculateForceMultiplier }}{{ $t('times') }}</div>
        <div class="rules"
             @click="goRuler">
          <img src="@/assets/rules.png">
          <div class="name">{{$t('rules')}}</div>
        </div>
      </div>
      <div class="tips"
           v-if="showErrorTip">*{{ $t('errtip') }}</div>
      <div class="line"></div>
      <div class="calculation">
        <div class="item"
             v-for="(item, index) in list"
             :key="index">
          <div class="name">{{ $t(item.name) }}</div>
          <div class="data"
               @click="choose(index)">
            <div class="name">
              <template v-if="item.value && index == 0">
                <van-loading size="24"
                             v-if="stakePeriodLoading" />
                <template v-else>
                  <div class="value">
                    <template v-if="item.value != '-'">{{
                      stakeDay + $t(item.value)
                    }}</template>
                    <template v-else>-</template>
                  </div>
                  <div class="lable"
                       v-if="item.lable && index != 3 && item.value != '-'"
                       :style="{ marginTop: item.value ? '1px' : '' }">
                    {{ item.lable }}{{ $t('times') }}
                  </div>
                </template>
              </template>
              <template v-else-if="item.value && index == 4">
                <div class="value"
                     v-if="item.value">
                  <template v-if="item.value != '-'">{{
                    item.value | NumFormat
                  }}</template>
                  <template v-else>-</template>
                </div>
              </template>
              <template v-else>
                <template v-if="index == 1">
                  <div class="value">
                    {{ index == list.length - 1 ? '$' : '' }}{{ item.value
                    }}{{ index == 1 ? $t('timesFaster') : '' }}
                    <!-- <div class="multiple"></div> -->
                  </div>
                </template>
                <template v-else>
                  <div class="value">
                    {{ index == 3 ? '$' : ''
                    }}{{ item.value | NumFormat }}
                  </div>
                </template>
              </template>

              <div class="lable"
                   v-if="item.lable && index == 4"
                   :style="{ marginTop: item.value ? '1px' : '' }">
                ≈ ${{ item.lable | NumFormat }}
              </div>
            </div>
            <div class="itemDown"
                 :style="{
                height: item.height || '7px',
                width: item.width || '13px',
                marginLeft: item.left || '13px',
              }">
              <img v-if="item.img"
                   :src="item.img"
                   @click="showTip(index)" />
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-button type="info"
                :disabled="btnDisabled"
                :loading="btnLoading"
                class="btns"
                color="#1F75D9"
                @click="stake">{{ $t(btnText) }}</van-button>
    <!-- <div class="soon">{{ $t('ComingSoon') }}</div> -->
    <van-action-sheet v-model="show"
                      :cancel-text="$t('cancel')">
      <div class="actionStyle"
           v-for="(el, index) in actions"
           :key="index"
           @click="onSelect(el)">
        {{ el.value + $t(el.name) + ' ' + el.percentage + $t(el.multiple) }}
      </div>
    </van-action-sheet>
    <van-dialog v-model="showTips"
                :confirm-button-text="$t('confirm')"
                :title="$t('calculatedFormula')"
                confirmButtonColor="#1F75D9">
      <div class="tipsBox">
        <div>{{ $t('income') }}</div>
        <div>{{ $t('current') }}</div>
        <div>{{ $t('actual') }}</div>
      </div>
    </van-dialog>
    <van-dialog v-model="showDayTips"
                :confirm-button-text="$t('confirm')"
                class="showDayTips"
                :title="$t('differentPower')"
                confirmButtonColor="#1F75D9">
      <div class="tipsDayBox">
        <div class="item">
          <div>
            {{
              $t('value') +
              '100-999USDT' +
              $t('is') +
              'LP,　1' +
              $t('hashrate')
            }}
          </div>
        </div>
        <div class="item">
          <div>
            {{
              $t('value') +
              '1000-1999USDT' +
              $t('is') +
              'LP,　1.5' +
              $t('hashrate')
            }}
          </div>
        </div>
        <div class="item">
          <div>
            {{
              $t('value') +
              '2000-2999USDT' +
              $t('is') +
              'LP,　2' +
              $t('hashrate')
            }}
          </div>
        </div>
        <div class="item">
          <div>
            {{
              $t('value') +
              '3000-4999USDT' +
              $t('is') +
              'LP,　2.5' +
              $t('hashrate')
            }}
          </div>
        </div>
        <div class="item">
          <div>
            {{
              $t('value') +
              '5000USDT以上' +
              $t('is') +
              'LP,　3' +
              $t('hashrate')
            }}
          </div>
        </div>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import StakePeriodImg from '@/assets/down.png'
import EstimatedIncomeToadyImg from '@/assets/mark.png'
import {
  ProxyAddress,
  LPAddress,
  WBNBAddress,
  srsAddress,
  usdtAddress,
  inputLpToUsdtNumber,
} from '@/util/casinoContract.js'
import {
  Toast
} from 'vant'
import {
  NumFormat,
  getUnixToDate
} from '@/util/filter.js'
import {
  Debounce
} from '@/util/Debounce.js'
import BigToSmall from '@/util/BNUtils.js'
export default {
  name: 'pool',
  props: ['Address'],
  data () {
    return {
      number: '',
      balance: 0,
      show: false,
      userAddress: '',
      showTips: false,
      showDayTips: false,
      showErrorTip: false,
      inputDisabled: false,
      stakePeriodLoading: true,
      activeLine: '', // 当前选中的百分比
      calculateForceMultiplier: 1, // 算力倍数
      totalPower: 0, // 矿池总算力
      percentage: 0, // 滑块百分比
      BlockNumber: '', // 当前块
      startBlock: '', // 开始块
      getReward: '', // 每区块奖励数量
      allResidualReward: 0, // 剩余奖励
      stakeDay: 30, // 质押周期
      totalLp: '', // 总质押lp
      myTotal: '', // 我质押的总lp
      btnDisabled: false,
      btnLoading: false,
      btnText: 'approve',
      allowance: 0, // 授权数量
      oneLpToUsdtPrice: 0, // 1个LP价值多少USDT
      linesList: [{
        lable: '25%',
        value: 0.25,
      },
      {
        lable: '50%',
        value: 0.5,
      },
      {
        lable: '75%',
        value: 0.75,
      },
      {
        lable: '100%',
        value: 1,
      },
      ],
      actions: [{
        name: 'days',
        value: 30,
        percentage: '1',
        multiple: 'times',
      },
      {
        name: 'days',
        value: 90,
        percentage: '1.5',
        multiple: 'times',
      },
      {
        name: 'days',
        value: 180,
        percentage: '2',
        multiple: 'times',
      },
      {
        name: 'days',
        value: 365,
        percentage: '3',
        multiple: 'times',
      },
      ],
      list: [{
        name: 'stakePeriod',
        img: StakePeriodImg,
        value: 'days',
        lable: '1.5',
        percentage: '-',
      },
      {
        name: 'totalHashrate',
        value: 1,
        lable: '',
      },
      {
        name: 'dailyIncomeCoefficient',
        value: '0',
        lable: '',
      },
      {
        name: 'estimatedOfIncomeToday',
        img: EstimatedIncomeToadyImg,
        lable: '',
        value: '0',
        left: '9px',
        height: '17px',
        width: '17px',
      },
      {
        name: 'totalLPLocked',
        value: '',
        lable: '0',
      },
      {
        name: 'myLPLocked',
        value: '',
        lable: '-',
      }
      ],
    }
  },
  filters: {
    NumFormat,
  },
  watch: {
    Address: function (val) {
      this.userAddress = val
      this.register(this.userAddress)
    },
  },
  computed: {
    WEB () {
      return this.$store.state.WEB3
    }
  },
  mounted () {
    if (this.Address) {
      this.userAddress = this.Address
      this.register(this.userAddress)
    }
    this.$bus.on('redeemBusEvent', async () => {
      await this.getAddressInfo('refresh')
    })
  },
  methods: {
    // 销毁
    userDestroy () {
      this.$router.push({
        path: 'userDestroy'
      })
    },
    // 质押
    async stake () {
      console.log('授权查询数量', this.allowance)
      if (!this.allowance) {
        // 暂无授权数量
        this.obtainAuthorization()
      } else {
        // 已有授权数量
        if (!this.number) return
        if (this.showErrorTip) return
        this.btnLoading = true
        if (this.number <= this.allowance) {
          // 正常
          console.log('授权数量大于输入数量，执行质押')
          this.getStakePair()
        } else {
          // 当前授权数量小于输入数量
          // 再次授权
          this.obtainAuthorization()
        }
      }
    },
    // 获取授权
    async obtainAuthorization () {
      this.btnLoading = true
      // 授权 BigToSmall.pow小转大 默认授权1亿
      let mount = BigToSmall.pow(100000000, 18)
      await this.$store.state.contractInstance.methods
        .approve(ProxyAddress, mount)
        .send({
          from: this.userAddress,
        })
        .then(async (res) => {
          this.btnLoading = false
          this.btnText = 'stake'
          console.log('res', res)
          this.allowance = await this.$store.state.contractInstance.methods
            .allowance(this.userAddress, ProxyAddress)
            .call()
          this.allowance = this.allowance / Math.pow(10, 18)
        })
        .catch((err) => {
          Toast(this.$t('cancel'))
          this.btnLoading = false
        })
    },
    // 质押方法
    getStakePair () {
      let mount = BigToSmall.pow(this.number, 18)
      console.log('当前质押数量', mount, this.stakeDay, this.userAddress)
      // return
      this.$store.state.poolContractInstance.methods
        .stakePair(this.stakeDay, mount)
        .send({
          from: this.userAddress,
        })
        .then(async (res) => {
          Toast(this.$t('success'))
          this.btnLoading = false
          this.$emit('jump')
          console.log('质押成功', res)
          // 刷新
          this.register(this.userAddress)
          this.$forceUpdate()
        })
        .catch((err) => {
          this.btnLoading = false
        })
    },
    // LP余额
    async getBalanceOf (contractInstance, val) {
      let balance = await contractInstance.methods.balanceOf(val).call()
      this.balance = +balance ? (balance / Math.pow(10, 18)).toFixed(4) : 0
      this.$store.dispatch('ActionsSetMyLp', this.balance)
      console.log('我的LP余额', this.balance)
    },
    async register (val) {
      console.log('缓存', this.$store.state)
      this.btnLoading = true
      // this.inputDisabled = true
      // 查询授权 数量
      let contractInstance = this.$store.state.contractInstance
      this.allowance = await contractInstance.methods
        .allowance(val, ProxyAddress)
        .call()
      this.allowance = this.allowance / Math.pow(10, 18)
      if (this.allowance) this.btnText = 'stake'
      console.log('查询授权 数量', this.allowance)
      this.getBalanceOf(contractInstance, val)
      // 余额加载完毕去除加载中
      // if (this.$refs.loadingCover) this.$refs.loadingCover.hide();
      // 1个LP的价格/单位usdt
      this.oneLpToUsdt()
      // 算奖励
      await this.getSrsReward()
      await this.getStarkblock()
      await this.getRewards()
      this.getResidualReward()
      this.allLp()
      this.myallLp()
      this.getEstimate()
      await this.getAddressInfo()
    },
    // 当前块
    async getSrsReward () {
      this.BlockNumber = await this.WEB.eth.getBlockNumber()
      console.log('当前块', this.BlockNumber)
    },
    // 开始块
    async getStarkblock () {
      this.startBlock = await this.$store.state.poolContractInstance.methods
        .startBlock()
        .call()
      console.log('开始块', this.startBlock)
    },
    //每区块奖励数量
    async getRewards () {
      this.reward = await this.$store.state.poolContractInstance.methods
        .blockAward()
        .call()
      this.reward = this.reward / Math.pow(10, 18)
      console.log('每区块奖励数量', this.reward)
    },
    // 剩余奖励
    // 矿池总奖励 -（当前-开始）*每区块奖励数量
    getResidualReward () {
      this.allResidualReward = 50000000 - (this.BlockNumber - this.startBlock) * this.reward
      console.log('剩余奖励', this.allResidualReward)
    },
    // 1个lp价值多少USDT
    async oneLpToUsdt () {
      this.oneLpToUsdtPrice = await this.getLPNum(1)
    },
    // 总质押lp
    async allLp () {
      let total = await this.$store.state.poolContractInstance.methods
        .totalCapital()
        .call()
      this.totalLp = total / Math.pow(10, 18)
      this.list[4].value = this.totalLp ? this.totalLp : '0'
      console.log('总质押', this.totalLp)
      await this.getLPNum(this.totalLp)
      // 总锁定lp/lp的发行量*lp中wbnb数量*wbnb价格
    },
    // lp的发行量
    async getLPNum (val) {
      let total = await this.$store.state.LPcontractInstance.methods
        .totalSupply()
        .call()
      total = total / Math.pow(10, 18)
      console.log('lp的发行量', total)
      // lp中wbnb数量
      let wbnbTotal = await this.$store.state.WBNBcontractInstance.methods
        .balanceOf(LPAddress)
        .call()
      wbnbTotal = wbnbTotal / Math.pow(10, 18)
      let wbnbPrice = await this.getWBNBPrice()
      console.log('lp中wbnb数量', wbnbTotal)
      console.log('总锁定的价格', (val / total) * wbnbTotal * wbnbPrice)
      this.list[4].lable = val ?
        ((val / total) * wbnbTotal * wbnbPrice).toFixed(4) :
        '0'
      this.$forceUpdate()
      return this.list[4].lable
    },
    async getWBNBPrice () {
      const wbnbNum = (1 * Math.pow(10, 18)).toString()
      // wbnb地址 // usdt地址
      let arr = [WBNBAddress, usdtAddress]
      let wbnbPrice = await this.$store.state.routercontractInstance.methods
        .getAmountsOut(wbnbNum, arr)
        .call()
      console.log('wbnbPrice', wbnbPrice)
      return wbnbPrice[1] / Math.pow(10, 18)
    },
    // 我质押的总lp
    async myallLp () {
      this.myTotal = await this.$store.state.poolContractInstance.methods
        .getAccountInfoSum(this.userAddress)
        .call()
      this.$store.dispatch('ActionsSetGetAccountInfoSum', this.myTotal)
      // 固定第二个 // 第三个总收益
      console.log('我锁定的总lp', this.myTotal)
      this.list[5].lable = ''
      this.list[5].value = +this.myTotal['1'] ?
        this.myTotal['1'] / Math.pow(10, 18) :
        '0'
      this.$forceUpdate()
    },
    // 获取预估
    // 今日预估收益 矿池每日产出量*我的总算力/矿池总算力*srs的价格
    async getEstimate () {
      // 矿池总算力totalPower
      this.totalPower = await this.$store.state.poolContractInstance.methods
        .totalPower()
        .call()
      // 矿池每日产出量 每个区块奖励blockAward*24*60*20
      let poweNum = this.reward * 24 * 60 * 20
      // 我的总算力 getAccountInfoSum 第一个参数
      let myPower = this.myTotal['0']
      let srsPrice = await this.getSrsPrice()
      let nowDayEstimate = +myPower ?
        (((poweNum * myPower) / this.totalPower) * srsPrice).toFixed(4) :
        '0'
      this.list[3].value = nowDayEstimate
      this.list[2].value = 1 / this.totalPower
      this.$forceUpdate()
      console.log(
        '矿池总算力',
        this.totalPower
      )
      console.log('日化收益系数', 1 / this.totalPower)
      console.log('this.list', this.list)
    },
    //srs的价格
    async getSrsPrice () {
      const srsNum = (1 * Math.pow(10, 18)).toString()
      // srs地址 wbnb地址 usdt地址
      let arr = [srsAddress, WBNBAddress, usdtAddress]
      let srsbPrice = await this.$store.state.routercontractInstance.methods
        .getAmountsOut(srsNum, arr)
        .call()
      console.log('srsbPrice', srsbPrice)
      return srsbPrice[2] / Math.pow(10, 18)
    },
    // 获取质押历史
    async getAddressInfo (refresh) {
      let startBlock = await this.$store.state.poolContractInstance.methods
        .startBlock()
        .call()
      // console.log('开始块', startBlock)
      // console.log(this.$store.state.poolContractInstance)
      let nowBlockNumber = await this.WEB.eth.getBlockNumber()
      // console.log('当前块nowBlockNumber', nowBlockNumber)
      let nowBlock = await this.WEB.eth.getBlock(startBlock)
      let time = getUnixToDate('yyyy-mm-dd hh:mm:ss', nowBlock.timestamp)
      // console.log('当前块时间', nowBlock.timestamp)
      let infoList = []
      // capital 用户本金
      // historyAward 历史累计收割奖励
      // accAward 累计奖励
      // stakeBlock 用户质押开始块
      // redeem（当前块>用户质押开始块+用户质押周期）是否可以赎回
      let Info_30 = await this.$store.state.poolContractInstance.methods
        .getAddressInfo(this.userAddress, 30)
        .call()
      let Info_60 = await this.$store.state.poolContractInstance.methods
        .getAddressInfo(this.userAddress, 90)
        .call()
      let Info_90 = await this.$store.state.poolContractInstance.methods
        .getAddressInfo(this.userAddress, 180)
        .call()
      let Info_365 = await this.$store.state.poolContractInstance.methods
        .getAddressInfo(this.userAddress, 365)
        .call()
      if (Info_30.capital != 0) {
        Info_30.day = 30
        Info_30.time = time
        Info_30.redeem =
          nowBlockNumber > +Info_30.stakeBlock + 30 ? true : false
        // console.log('Info_30.redeem', Info_30.redeem)
        // console.log('Info_30.redeem', nowBlockNumber, (Info_30.stakeBlock + 30))
        infoList.push(Info_30)
      }
      if (Info_60.capital != 0) {
        Info_60.day = 90
        Info_60.time = time
        Info_60.redeem =
          nowBlockNumber > +Info_60.stakeBlock + 60 ? true : false
        infoList.push(Info_60)
      }
      if (Info_90.capital != 0) {
        Info_90.day = 180
        Info_90.time = time
        Info_90.redeem =
          nowBlockNumber > +Info_90.stakeBlock + 90 ? true : false
        infoList.push(Info_90)
      }
      if (Info_365.capital != 0) {
        Info_365.day = 365
        Info_365.time = time
        Info_365.redeem =
          nowBlockNumber > +Info_365.stakeBlock + 365 ? true : false
        infoList.push(Info_365)
      }
      console.log('获取质押历史', infoList, this.actions)
      if (infoList.length == this.actions.length) {
        for (let i = 0; i < this.actions.length; i++) {
          this.actions.splice(i, 1)
          i--
        }
        this.inputDisabled = true
        this.btnDisabled = true
        this.list[0].value = '-'
      } else {
        if (!refresh) {
          for (let i in this.actions) {
            for (let j in infoList) {
              if (this.actions[i].value == infoList[j].day) {
                this.actions.splice(i, 1)
                this.list[0].value = this.actions[0].name
                this.list[0].percentage = this.actions[0].percentage
                this.stakeDay = this.actions[0].value
              }
            }
          }
        } else {
          let item = [30, 90, 180, 365]
          this.actions = []
          console.log('返回', infoList, this.actions)
          if (infoList.length == 4) {
            this.inputDisabled = true
            this.btnDisabled = true
            this.list[0].value = '-'
          } else {
            let arr1 = []
            for (let i in infoList) {
              arr1.push(infoList[i].day)
            }

            function diff (arr1, arr2) {
              var newArr1 = arr1.filter((value) => arr2.indexOf(value) == -1)
              var newArr2 = arr2.filter((value) => arr1.indexOf(value) == -1)
              return newArr1.concat(newArr2)
            }
            let diffList = diff(arr1, item)
            for (let j in diffList) {
              this.actions.push({
                name: 'days',
                value: diffList[j],
                multiple: 'times',
                percentage: diffList[j] == 30 ?
                  1 :
                  diffList[j] == 90 ?
                    1.5 :
                    diffList[j] == 180 ?
                      2 :
                      3,
              })
            }
          }
        }
        this.list[0].lable = this.actions[0].percentage
        this.list[0].value = this.actions[0].name
        this.stakeDay = this.actions[0].value
        this.list[0].percentage = this.actions[0].percentage
        this.list[1].value = +this.actions[0].percentage * this.calculateForceMultiplier || 1
        this.btnDisabled = false
        // if (this.allowance) this.inputDisabled = false
        // console.log('this.actions', this.actions)
        console.log('this.list', this.list)
      }
      this.btnLoading = false
      this.stakePeriodLoading = false
      this.$forceUpdate()
    },
    choose (i) {
      if (i == 0 && !this.stakePeriodLoading) {
        if (this.actions.length) {
          this.show = true
        } else {
          Toast(this.$t('btnDisabled'))
        }
      }
    },
    onSelect (item) {
      console.log(item)
      this.stakeDay = item.value
      this.list[0].value = item.name
      this.list[0].lable = item.percentage
      this.list[1].value = +this.list[0].lable ?
        +this.list[0].lable * this.calculateForceMultiplier :
        0
      this.show = false
      this.$forceUpdate()
    },
    showTip (i) {
      if (i == 3) this.showTips = true
    },
    inputChange: Debounce(function (val) {
      let minPL =
        Math.ceil((inputLpToUsdtNumber / +this.oneLpToUsdtPrice) * 100) / 100 // 保留2位 小数向上取整
      // 没有授权数量按钮不变化
      if (!this.allowance) {
        if (+val > this.balance) {
          Toast(this.$t('errtip'))
          return
        } else if (+val < minPL) {
          Toast(this.$t('minLpNum') + minPL)
          return
        }
      }

      if (!val) {
        this.showErrorTip = false
        return
      }
      if (+this.balance < +val) {
        this.showErrorTip = true
      } else {
        if (+val < minPL) {
          this.btnDisabled = true
          this.showErrorTip = true
          Toast.clear()
          if (val && val != 0) Toast(this.$t('minLpNum') + minPL)
        } else {
          this.btnDisabled = false
          this.showErrorTip = false
        }
        this.showErrorTip = false
      }
      this.countInputNumber(val)
      // console.log('一个LP的价格/usdt', this.oneLpToUsdtPrice, minPL)
    }, 1000),
    allstake () {
      if (!this.inputDisabled) {
        this.percentage = 100
        this.number = this.balance
      }
    },
    countInputNumber (num) {
      let allUSDT = this.oneLpToUsdtPrice * num
      console.log('总价值usdt', allUSDT)
      if (allUSDT < 99) {
        this.calculateForceMultiplier = 1
      } else if (allUSDT >= 100 && allUSDT <= 999) {
        this.calculateForceMultiplier = 1
      } else if (allUSDT >= 1000 && allUSDT <= 1999) {
        this.calculateForceMultiplier = 1.5
      } else if (allUSDT >= 2000 && allUSDT <= 2999) {
        this.calculateForceMultiplier = 2
      } else if (allUSDT >= 3000 && allUSDT <= 4999) {
        this.calculateForceMultiplier = 2.5
      } else if (allUSDT >= 5000) {
        this.calculateForceMultiplier = 3
      }
      this.list[1].value = +this.list[0].lable ?
        +this.list[0].lable * this.calculateForceMultiplier :
        0
    },
    activeLineChange (i, val) {
      // if (!this.allowance) return
      if (this.stakePeriodLoading) return
      this.activeLine = i
      this.number = this.balance * val
      this.countInputNumber(this.number)
      this.inputChange(this.number)
    },
    goRuler () {
      this.$router.push({
        path: 'rules'
      })
    }
  },
}
</script>

